import TechCard from "../ui/tech-card";
import * as React from "react";
import {
  ContextMenu,
  ContextMenuContent,
  ContextMenuItem,
  ContextMenuSeparator,
  ContextMenuTrigger,
  ContextMenuSub,
  ContextMenuSubContent,
  ContextMenuSubTrigger,
  ContextMenuRadioGroup,
  ContextMenuRadioItem,
} from "@/components/ui/context-menu";
import { useState, useEffect } from "react";
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
} from "@dnd-kit/core";
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  verticalListSortingStrategy,
  useSortable,
} from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";
import { Button } from "@/components/ui/button";
import { GripVerticalIcon } from "lucide-react";

// D√©finition du type Tech avec la taille de la carte
export type Tech = {
  id: string;
  name: string;
  color: string;
  icon: React.ReactNode;
  category?: string;
  technologyId?: string;
  gridSpan?: {
    cols?: 1 | 2 | 3;
    rows?: 1 | 2;
  };
  // Ajout d'un champ pour la position
  order?: number;
  // Champs pour les projets
  isProject?: boolean;
  favicon?: string;
  url?: string;
  description?: string;
};

type TechStackGridProps = {
  technologies: Tech[];
  onRemoveTech?: (id: string) => void;
  onUpdateTech?: (id: string, updates: Partial<Tech>) => void;
  onReorderTechs?: (reorderedTechs: Tech[]) => void;
};

// Composant pour un √©l√©ment draggable individuel
function SortableItem({ id, tech }: { id: string; tech: Tech }) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    zIndex: isDragging ? 10 : undefined,
    opacity: isDragging ? 0.8 : 1,
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      className={`p-1 rounded-lg border-2 transition-all ${
        isDragging
          ? "border-primary shadow-lg bg-card/50"
          : "border-transparent"
      }`}
    >
      <div className="flex items-center gap-2 p-2 bg-card rounded-lg border border-border">
        <div
          {...attributes}
          {...listeners}
          className="cursor-grab p-1 hover:bg-muted rounded"
        >
          <GripVerticalIcon size={18} />
        </div>
        <div className="flex items-center gap-3 flex-1">
          <div className="flex items-center justify-center w-8 h-8">
            {tech.icon}
          </div>
          <div>
            <p className="font-medium">{tech.name}</p>
            <div
              className="w-4 h-1 rounded-full mt-1"
              style={{ backgroundColor: tech.color }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default function TechStackGrid({
  technologies,
  onRemoveTech,
  onUpdateTech,
  onReorderTechs,
}: TechStackGridProps) {
  const [isEditMode, setIsEditMode] = useState(false);
  const [items, setItems] = useState<Tech[]>([]);

  useEffect(() => {
    // S'assurer que les items ont un ordre initial et sont tri√©s
    const orderedTechnologies = [...technologies]
      .map((tech, index) => ({
        ...tech,
        order: tech.order !== undefined ? tech.order : index,
      }))
      .sort((a, b) => (a.order ?? 0) - (b.order ?? 0));
    console.log(
      "TechStackGrid useEffect: Technologies prop updated, re-sorting and setting items:",
      JSON.parse(
        JSON.stringify(
          orderedTechnologies.map((t) => ({
            id: t.id,
            name: t.name,
            order: t.order,
          }))
        )
      )
    );
    setItems(orderedTechnologies);
  }, [technologies]);

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;

    if (over && active.id !== over.id) {
      // It's important to use the `items` from the outer scope for findIndex before setItems,
      // or ensure `currentItemsInternal` is correctly sourced if used for these initial indices.
      const oldIndex = items.findIndex(
        (item) => String(item.id) === String(active.id)
      );
      const newIndex = items.findIndex(
        (item) => String(item.id) === String(over.id)
      );

      console.log(
        `TechStackGrid handleDragEnd: Attempting to move item from oldIndex: ${oldIndex} to newIndex: ${newIndex}`
      );
      console.log(
        "TechStackGrid handleDragEnd: Active item ID:",
        active.id,
        "Over item ID:",
        over.id
      );
      console.log(
        "TechStackGrid handleDragEnd: Current items before move (snapshot):",
        JSON.parse(
          JSON.stringify(
            items.map((t) => ({ id: t.id, name: t.name, order: t.order }))
          )
        )
      );

      if (oldIndex === -1 || newIndex === -1) {
        console.error(
          "TechStackGrid handleDragEnd: Could not find old or new index for items.",
          { activeId: active.id, overId: over.id, oldIndex, newIndex, items }
        );
        return;
      }

      setItems((currentItemsInternal) => {
        const oldIndexInternal = currentItemsInternal.findIndex(
          (item) => String(item.id) === String(active.id)
        );
        const newIndexInternal = currentItemsInternal.findIndex(
          (item) => String(item.id) === String(over.id)
        );

        if (oldIndexInternal === -1 || newIndexInternal === -1) {
          console.error(
            "TechStackGrid handleDragEnd (inside setItems): Could not find old or new index.",
            {
              activeId: active.id,
              overId: over.id,
              oldIndexInternal,
              newIndexInternal,
            }
          );
          return currentItemsInternal;
        }

        console.log(
          `TechStackGrid handleDragEnd (inside setItems): Moving from oldIndexInternal: ${oldIndexInternal} to newIndexInternal: ${newIndexInternal}`
        );
        const reordered = arrayMove(
          currentItemsInternal,
          oldIndexInternal,
          newIndexInternal
        );
        const finalItems = reordered.map((item, index) => ({
          ...item,
          order: index,
        }));

        console.log(
          "TechStackGrid handleDragEnd: Items after local reorder (order property updated):",
          JSON.parse(
            JSON.stringify(
              finalItems.map((t) => ({
                id: t.id,
                name: t.name,
                order: t.order,
              }))
            )
          )
        );

        if (onReorderTechs) {
          onReorderTechs(finalItems);
        }
        return finalItems;
      });
    }
  };

  const handleUpdateGridSpan = (
    techId: string,
    span: { cols?: 1 | 2 | 3; rows?: 1 | 2 }
  ) => {
    if (onUpdateTech) {
      onUpdateTech(techId, { gridSpan: span });
    }
  };

  if (isEditMode) {
    return (
      <div className="space-y-4">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold">Mode r√©organisation</h3>
          <Button onClick={() => setIsEditMode(false)} variant="outline">
            Terminer
          </Button>
        </div>

        <DndContext
          sensors={sensors}
          collisionDetection={closestCenter}
          onDragEnd={handleDragEnd}
        >
          <SortableContext
            items={items.map((item) => item.id)}
            strategy={verticalListSortingStrategy}
          >
            <div className="space-y-2">
              {items.map((tech) => (
                <SortableItem key={tech.id} id={tech.id} tech={tech} />
              ))}
            </div>
          </SortableContext>
        </DndContext>
      </div>
    );
  }

  // Affichage normal de la grille (tri√© par 'order')
  return (
    <div className="space-y-4">
      <div className="flex justify-end mb-4">
        <Button
          onClick={() => setIsEditMode(true)}
          variant="outline"
          className="text-sm"
        >
          R√©organiser
        </Button>
      </div>

      <div className="grid grid-cols-3 gap-6">
        {items.map((tech) => {
          const colSpanToApply = tech.gridSpan?.cols || 1;
          const rowSpanToApply = tech.gridSpan?.rows || 1;
          const colSpanClass =
            colSpanToApply === 3
              ? "col-span-3"
              : colSpanToApply === 2
              ? "col-span-2"
              : "col-span-1";
          const rowSpanClass =
            rowSpanToApply === 2 ? "row-span-2" : "row-span-1";

          return (
            <div
              className={`${colSpanClass} ${rowSpanClass}`}
              key={String(tech.id)}
            >
              <ContextMenu>
                <ContextMenuTrigger>
                  <div className="relative transition-all duration-300 ease-in-out h-full w-full">
                    <TechCard
                      id={tech.id}
                      name={tech.name}
                      color={tech.color}
                      icon={tech.icon}
                      gridSpan={tech.gridSpan}
                    />
                  </div>
                </ContextMenuTrigger>
                <ContextMenuContent>
                  <ContextMenuItem
                    variant="destructive"
                    onClick={() => onRemoveTech && onRemoveTech(tech.id)}
                  >
                    Remove
                  </ContextMenuItem>
                  <ContextMenuSeparator />
                  <ContextMenuSub>
                    <ContextMenuSubTrigger>Column Span</ContextMenuSubTrigger>
                    <ContextMenuSubContent>
                      <ContextMenuRadioGroup
                        value={String(tech.gridSpan?.cols || 1)}
                        onValueChange={(value) => {
                          const numValue = parseInt(value, 10) as 1 | 2 | 3;
                          handleUpdateGridSpan(tech.id, {
                            cols: numValue,
                            rows: tech.gridSpan?.rows || 1,
                          });
                        }}
                      >
                        <ContextMenuRadioItem value="1">
                          1 Column
                        </ContextMenuRadioItem>
                        <ContextMenuRadioItem value="2">
                          2 Columns
                        </ContextMenuRadioItem>
                        <ContextMenuRadioItem value="3">
                          Full Width
                        </ContextMenuRadioItem>
                      </ContextMenuRadioGroup>
                    </ContextMenuSubContent>
                  </ContextMenuSub>
                  <ContextMenuSub>
                    <ContextMenuSubTrigger>Row Span</ContextMenuSubTrigger>
                    <ContextMenuSubContent>
                      <ContextMenuRadioGroup
                        value={String(tech.gridSpan?.rows || 1)}
                        onValueChange={(value) => {
                          const numValue = parseInt(value, 10) as 1 | 2;
                          handleUpdateGridSpan(tech.id, {
                            cols: tech.gridSpan?.cols || 1,
                            rows: numValue,
                          });
                        }}
                      >
                        <ContextMenuRadioItem value="1">
                          1 Row
                        </ContextMenuRadioItem>
                        <ContextMenuRadioItem value="2">
                          2 Rows
                        </ContextMenuRadioItem>
                      </ContextMenuRadioGroup>
                    </ContextMenuSubContent>
                  </ContextMenuSub>
                </ContextMenuContent>
              </ContextMenu>
            </div>
          );
        })}
      </div>
    </div>
  );
}
